import React from 'react'

import { Header } from '../components/Header'

import '../style/About.scss'

export function About() {
    return (
        <div id="About">
            <div className="header-content">
                <Header/>
            </div>
            <div className="principal-content-about">
                <div className="intro-content-about">
                    <h1>Sou desenvolvedor Web &amp; Mobile</h1>
               </div>
               <div className="about-text-content">
                   <h1>Sobre mim</h1>
                   <h3>Me chamo Gabriel Aparecido,tenho 18 anos.Moro em Sorocaba,interior de So Paulo.Sou um entusiasta de tecnologias web &amp; mobile.</h3>
                    <p>
                        No meu dia a dia gosto de usar tecnologias como:Typescript,ReactJS,Firebase,React Native e Sass.
                    </p>
                    <p>
                        Comecei a estudar programao com 12 anos,aprendendo lgica de programao e algoritmo em VisualAlg,depois fiz projeto simples no Scratch e tambm fazendo jogos simples no Construct 2.Porm com meus 14 anos decidi focar no vestibular,e desse mode deixando de lado a rea da programao.Porm no ano de 2019,voltei a estuda tecnologia.
                    </p>
                    <p>
                        Amo o sentimento de desafio.No sou uma pessoa de recuar ao um bom desafio,gosto de me sentir,por tal motivo sempre busco pro projetos que possam ser teis e que gerem dificultades.Sempre busco cada vez me tornar mais resiliente e paciente.
                    </p>    
                    <p>
                        Gosto de desenvolver conhecimentos em vrias reas,j li livros como :Uma Breve Histria do Tempo do Stephen Hawking,Incognito do David Eagleman,Rpido e Devagar do David Eagleman,Mochileiros das Galxias.
                    </p>
               </div>
               <div className="pdf-content">
                   <h1>Para obter mais detalhes baixe currculo em pdf</h1>
                   <p>Baixar pdf</p>
               </div>
            </div>

        </div>
    )
}